    z-index: 1;
}

.dropdown-content a {
    color: black;
    padding: 5px 20px;
    text-decoration: none;
    display: block;
}

.dropdown-content a:hover {background-color: gray;}

.dropdown:hover .dropdown-content {display: block;}




