.main-nav.sticky-on.sticked {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    -webkit-animation: fadeInDown 1s alternate;
    animation: fadeInDown 1s alternate;
}
.main-nav.sticky-on {
    position: relative;
    top: auto;
    left: auto;
    bottom: auto;
    right: auto;
    background-color: #fff;
    box-shadow: 2px 2px 4px rgba(0,0,0,0.1);
}
.main-nav {
    position: absolute;
    bottom: -1px;
    left: 0;
